import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { VendorComponent } from './vendor/vendor.component';
import { AddvendorComponent } from './vendor/addvendor/addvendor.component';
import { ContactComponent } from './vendor/contact/contact.component';
// import { ContactComponent } from './contact/contact.component';




const routes: Routes = [
  {path:'',component:LoginComponent},
  {path:'vendor',component:VendorComponent},
  {path:'addvendor',component:AddvendorComponent},
  {path:'editVendor/:id',component:AddvendorComponent},
  {path:'viewcontact/:id',component:ContactComponent}

  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
