import { Component, OnInit } from '@angular/core';
import { Vendor } from '../vendor';
import { Router, ActivatedRoute } from '@angular/router';
import { VendorService } from '../vendor.service';

@Component({
  selector: 'app-addvendor',
  templateUrl: './addvendor.component.html',
  styleUrls: ['./addvendor.component.scss']
})
export class AddvendorComponent implements OnInit {
  vendor = new Vendor();
  vendors:Vendor[];
  vendorId:any;
  constructor(private router: Router,private route:ActivatedRoute,private _service: VendorService) { }

  ngOnInit() {
    this.getVendorById();
    this.resetform();
  }

  resetform() {
    this._service.vendor = {
      vendorId: null,
      name: '',
      address: '',
      location: '',
      service: '',
      pincode: '',
      isActive: '',
      id: '',
      contactname: '',
      department: '',
      email: '',
      phoneno: '',
      
    }
  }


  saveVendor() {
    this.resetform();
    this._service.insertVendor(this.vendor)
    .subscribe((response)=>{
      
      console.log(response);
      // this.toastr.success('Saved successfully');
      this.router.navigate(['vendor'])
    }, (error)=>{
      console.log(error);
    });
  }
  getVendor():void{
    this._service.getAllVendors()
    .subscribe((data)=>{
      this.vendors=data;
      console.log(data);
    },(error)=>{
      console.log(error);
    });
  
  }
 

  getVendorById(){
    this.vendorId=this.route.snapshot.paramMap.get('id');
    this._service.getVendorbyId(this.vendorId)
    .subscribe((data)=>{
      this.vendor=data;
      console.log(data);
    },(error)=>{
      console.log(error);
    })
  }
 
}
