import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { VendorService } from './vendor.service';
import { Vendor } from './vendor';

@Component({
  selector: 'app-vendor',
  templateUrl: './vendor.component.html',
  styleUrls: ['./vendor.component.scss']
})
export class VendorComponent implements OnInit {

  constructor(private router: Router, private service: VendorService, private route: ActivatedRoute) { }
  vendors: Vendor[];
  vendor = new Vendor();
  searchText;
  searchid: any;

  ngOnInit() {
    this.getVendors();
  }
  //routing for add
  addvendor() {
    this.router.navigate(['addvendor'])
  }

  //routing for edit vendor
  editVendor(data: any) {
    this.router.navigate(['editVendor', data]);

  }
  
  //get all vendors
  getVendors() {
    this.service.getAllVendors()
      .subscribe((Data) => {
        this.vendors = Data;
        console.log(Data);
      }, (error) => {
        console.log(error);
      });
  }

  //disable vendor
  DisableVendor()
  {
    console.log("inside disable" +this.searchid);
    this.service.disableVendor(this.searchid)
    .subscribe((data)=>{
      console.log(data);
      this.getVendors();
    },(error)=>{
      console.log(error);
    });   
  }
  onSelect(selectedStaff: any) {
    console.log("Selected item Id:", selectedStaff.vendorId);
    this.searchid = selectedStaff.vendorId;
  }

  //view corressponding contact person
  view(vendorId:any){
    this.router.navigate(['viewcontact',vendorId])
  }


}
